/*стилизация input[type=range]*/
input[type=range].slider__range {
  width: 60%;
  margin: 12.8px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
@media (min-width: 768px) {
  input[type=range].slider__range {
    width: 75%;
  }
}
input[type=range].slider__range:focus {
  outline: none;
}
input[type=range].slider__range::-webkit-slider-runnable-track {
  background: #e6e6e6;
  border: 0.2px solid #d9d9d9;
  border-radius: 4px;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
input[type=range].slider__range::-webkit-slider-thumb {
  margin-top: -13px;
  width: 34px;
  height: 34px;
  background: #68b738;
  border: 13px solid #ffffff;
  border-radius: 16px;
  filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.16));
  cursor: pointer;
  -webkit-appearance: none;
}
input[type=range].slider__range:focus::-webkit-slider-runnable-track {
  background: #e6e6e6;
}
input[type=range].slider__range::-moz-range-track {
  background: #e6e6e6;
  border: 0.2px solid #d9d9d9;
  border-radius: 4px;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
input[type=range].slider__range::-moz-range-thumb {
  width: 34px;
  height: 34px;
  background: #68b738;
  border: 13px solid #ffffff;
  border-radius: 16px;
  filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.16));
  cursor: pointer;
}
input[type=range].slider__range::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 12.8px 0;
  color: transparent;
  width: 100%;
  height: 8.4px;
  cursor: pointer;
}
input[type=range].slider__range::-ms-fill-lower {
  background: #000000;
  border: 0.2px solid #d9d9d9;
  border-radius: 2.6px;
}
input[type=range].slider__range::-ms-fill-upper {
  background: #e6e6e6;
  border: 0.2px solid #d9d9d9;
  border-radius: 2.6px;
}
input[type=range].slider__range::-ms-thumb {
  width: 34px;
  height: 34px;
  background: #68b738;
  border: 13px solid #ffffff;
  border-radius: 16px;
  filter: drop-shadow(0px 1px 3px rgba(0, 0, 0, 0.16));
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type=range].slider__range:focus::-ms-fill-lower {
  background: #e6e6e6;
}
input[type=range].slider__range:focus::-ms-fill-upper {
  background: #ffffff;
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align:auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  input[type=range].slider__range {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}
